<script>
export default {
  name: "App",
  head: {
    meta: [
      { property: "og:site_name", content: "aworsley.me" },
      { property: "og:url", content: "https://aworsley.me" },
      { property: "og:title", content: "Adam - Software Developer" },
      {
        property: "og:description",
        content: "I'm a 16 year old software developer.",
      },
      { property: "og:type", content: "website" },
      { property: "site_name", content: "aworsley.me" },
      { property: "url", content: "https://aworsley.me" },
      { property: "title", content: "Adam - Software Developer" },
      {
        property: "description",
        content: "I'm a 19 year old software developer.",
      },
      { property: "type", content: "website" },
      { name: "theme-color", content: "#c3dafe" },
    ],
  },
  data() {
    return {
      api_key: "f70c286f74e127cb0cbdbbff01345bb9",
      url_base: "https://api.openweathermap.org/data/2.5/",
      song_url: "https://currently-playing-sigma.vercel.app/api/spotify",
      query: "manchester",
      weather: {},
      song: {},
    };
  },
  created() {
    this.fetchWeather();
    this.fetchSong();
  },
  methods: {
    fetchWeather() {
      fetch(
        `${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`
      )
        .then((res) => {
          return res.json();
        })
        .then(this.setWeatherResults);
    },
    setWeatherResults(result) {
      this.weather = result;
    },
    fetchSong() {
      fetch(`${this.song_url}`)
        .then((res) => {
          return res.json();
        })
        .then(this.setSongResults);
    },
    setSongResults(result) {
      this.song = result;
    },
  },
};
</script>

